.App-table-preload {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-table {
	height:1000px;
	display: flex;
	width:100%;
}
.App-table--row .option-name--cell {
	width: 100%;
}
.App-table--row .option-name--cell .option-name {
	width: 80%;
}
.App-table--row .option-name--cell:hover .option-name {
	overflow: hidden;
}
.App-table--row .option-name--cell .actions {
	width: 20%;
	cursor: pointer;
	padding-top: 8px;
}
.App-table--row .option-name--cell .icon-action {
	float: right;
}
.App-table--row .icon-action {
	visibility: hidden;
}
.App-table--row .icon-label.optionActiveTheme {
	visibility: visible;
	color:#00c8f4;
}
.App-table--row:hover .icon-preview {
	margin-right: 0.5em;
	visibility: visible;
	/*visibility: hidden;*/
}
.App-table--row .hidden {
	display:none;
}


