.App-mask-table-preload {
  background-color: #282c34;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-mask-table {
	height:500px;
	display: flex;
}
.App-mask-table--row .cell-actions {
	margin-top: 12px;	
	visibility: hidden;
	color: black;	
}
.App-mask-table--row .button-action {
	cursor: pointer;
	margin-right: 0.5em;
}
.App-mask-table--row.Mui-selected .cell-actions {
	/*visibility: visible;*/
}
.App-mask-table--row:hover .cell-actions {
	visibility: visible;
}
.App-mask-table--row .cell-actions .button-edit.button-hidden {
	display:none;
}
